import { Button, Col, Input, Row, Select } from 'antd';
import { useState } from 'react';

import ReactJson from 'react-json-view';
const { Option } = Select;
const { TextArea } = Input;

function getFormatJsonData(value: string, type: string) {
    try {
        let formatData = {}
        switch (type) {
            case '001':
                // eslint-disable-next-line no-new-func
                formatData = (new Function("return " + value))()
                break;
            case '002':
                // eslint-disable-next-line no-new-func
                formatData = (new Function("return " + atob(value)))()
                break;
            default:
                // eslint-disable-next-line no-new-func
                formatData = (new Function("return " + value))()
                break;
        }
        return formatData
    } catch (error) {
        console.log(error)
        return {}
    }
}

export function ViewJSon() {
    const [waitFormatString, setSaitFormatString] = useState('')
    const [dataType, setDataType] = useState('')
    const [formatJsonData, setFormatJsonData] = useState({})
    return (
        <>
            <div style={{ margin: 21 }}>
                <div>
                    <Select value={dataType} style={{ width: 300 }} onChange={(value) => { setDataType(value) }}>
                        <Option value="001">JSON</Option>
                        <Option value="002">base64JSONJSON</Option>
                    </Select>
                    <Button type='primary' onClick={() => { setFormatJsonData(getFormatJsonData(waitFormatString, dataType)) }}>JSON</Button>
                </div>
                <Row>
                    <Col span={12}>
                        <TextArea rows={4} value={waitFormatString} onChange={(e) => {
                            setSaitFormatString(e.target.value)
                        }} />
                    </Col>
                    <Col span={12}>
                        <div style={{ textAlign: 'left', padding: '21px 21px' }}>
                            <ReactJson
                                name={false} // JSON()
                                src={formatJsonData} // JSON
                                theme={'rjv-default'} // base-16
                                iconStyle={'circle'} // circle()triangle()square()
                                indentWidth={6} // 
                                collapsed={2} // 
                                collapseStringsAfterLength={false} // 
                                displayDataTypes={true} // 
                                displayObjectSize={true} // 
                            />
                        </div>
                    </Col>
                </Row>
            </div>
        </>
    )
}